<template>
	<div style="width: 100%;" name="el-fade-in-linear">
		<el-drawer title="管理列表" :direction="rtl" :visible.sync="drawerShow" size="75%" open="openGroupDrawer">
			<el-row style="width: 100%;margin: 0px 0px 2% 5%;">
				<el-button type="primary" disabled round>修改</el-button>
				<el-button type="success" disabled round>停止</el-button>
				<el-button type="danger" @click="delectGroup" round>删除组</el-button>
			</el-row>
			<el-table :data="userData" height="300" style="width: 100%;margin: 0px 0px 5% 5%;"
				@selection-change="handleSelectionChange" ref="selectActiveUser" tooltip-effect="dark">
				<el-table-column type="selection" width="55">
				</el-table-column>
				<el-table-column label="创建日期" width="250">
					<template slot-scope="scope">
						<i class="el-icon-time"></i>
						<span style="margin-left: 10px">{{ scope.row.createDate
							}}</span>
					</template>
				</el-table-column>
				<el-table-column prop="remark" label="备注" width="200">

				</el-table-column>

				<el-table-column label="姓名" width="150">
					<template slot-scope="scope">
						<el-popover trigger="hover" placement="top">
							<p>卡类型: {{ scope.row.cardType }}</p>
							<p>手机号: {{ scope.row.phone }}</p>
							<div slot="reference" class="name-wrapper">
								<el-tag size="medium">{{ scope.row.identityName }}</el-tag>
							</div>
						</el-popover>
					</template>
				</el-table-column>
				<el-table-column prop="setNumber" label="在组个数" width="170"></el-table-column>
				<el-table-column label="操作">
					<template>
						<el-tooltip class="item" effect="dark" content="只添加勾选的" placement="top">
							<el-button size="mini" @click="addGroupUser">添加</el-button>
						</el-tooltip>
					</template>
				</el-table-column>
			</el-table>

			<el-table :data="groupInsideUserData" height="350" style="width: 100%;margin-left: 5%;" ref="multipleTable"
				tooltip-effect="dark">
				<el-table-column label="创建日期" width="305">
					<template slot-scope="scope">
						<i class="el-icon-time"></i>
						<span style="margin-left: 10px">{{ scope.row.createDate
							}}</span>
					</template>
				</el-table-column>
				<el-table-column prop="remark" label="备注" width="200">

				</el-table-column>

				<el-table-column label="姓名" width="150">
					<template slot-scope="scope">
						<el-popover trigger="hover" placement="top">
							<p>cookie:{{ scope.row.cookie }}</p>
							<div slot="reference" class="name-wrapper">
								<el-tag size="medium">{{ scope.row.userName }}</el-tag>
							</div>
						</el-popover>
					</template>
				</el-table-column>
				<el-table-column prop="usage" label="请求次数" width="170"></el-table-column>
				<el-table-column label="操作">
					<template slot-scope="scope">
						<el-tooltip class="item" effect="dark" content="在本组内停止或开启" placement="top">
							<el-button size="mini" @click="stopUser(scope.row.groupId, scope.row.snowflakeId)"
								:disabled="!scope.row.isActive">
								{{scope.row.isSnatch ? '停止' : '开启'}}</el-button>
						</el-tooltip>
						<el-tooltip class="item" effect="dark" content="在本组内删除" placement="top">
							<el-button size="mini" type="danger"
								@click="delectUser(scope.row.groupId, scope.row.snowflakeId)">删除
							</el-button>
						</el-tooltip>

					</template>
				</el-table-column>
			</el-table>

		</el-drawer>

		<el-row>
			<el-col :spam="24">
				<img src="" />

				<div style="float: right;margin: 10px 20px 20px 0;">
					<el-button @click="showAdd ? showAdd=false : showAdd=true" type="primary" size="small"
						icon="el-icon-plus" circle>
					</el-button>
				</div>
			</el-col>

			<el-col :span="7" v-for="(group, index) in groupData" :key="index" :offset="index > 0 ? 1 : 0"
				name="el-fade-in">
				<el-card id="content" :body-style="{ padding: '0px' }" shadow="hover">
					<img src="https://img1.baidu.com/it/u=3421193089,2140635042&fm=253&fmt=auto&app=138&f=JPEG?w=800&h=500"
						class="image">
					<div style="padding: 14px;">
						<div style="font-weight: 350;margin: 5px 0px 20px 0px;">
							<span>{{group.remark}}</span>
						</div>
						<div style="font-size: 14px;font-weight: 300;" class="group_row">
							<span>当前状态：</span>
							<span class="status disabled" v-if="!group.isActive" style="float: right;">{{group.isActive
								?
								'开启' :
								'停止'}}</span>
							<span class="status active" v-if="group.isActive" style="float: right;">{{group.isActive
								?
								'开启' :
								'停止'}}</span>
						</div>
						<div style="font-size: 14px;font-weight: 300;" class="group_row">
							<span>组内人数：</span>
							<span style="float: right;">{{group.userSize}}
							</span>
						</div>
						<div style="font-size: 14px;font-weight: 300;" class="group_row">
							<span>成功下单数：</span>
							<span style="float: right;">{{group.orderNumber}}/{{group.activeUserSize}}
							</span>
						</div>

						<div style="font-size: 14px;font-weight: 300;" class="group_row">
							<span>任务医生：</span>
							<span style="float: right;">{{group.docter}}</span>
						</div>

						<div style="font-size: 14px;font-weight: 300;" class="group_row">
							<span>任务类型：</span>
							<span style="float: right;">{{group.taskType == 1 ? '单人轮询' : '未知'}}</span>
						</div>
						<div style="font-size: 14px;font-weight: 300;" class="group_row">
							<span>挂号日期：</span>
							<span style="float: right;">{{group.registerDate}}</span>
						</div>
						<div style="font-size: 14px;font-weight: 300;" class="group_row">
							<span>开始时间：</span>
							<span style="float: right;">{{group.taskStartDate}}</span>
						</div>
						<div style="font-size: 14px;font-weight: 300;" class="group_row">
							<span>创建时间：</span>
							<span style="float: right;">{{group.createDate}}
							</span>
						</div>

						<div class="bottom clearfix">
							<time class="time"></time>
							<el-button type="text" @click="openDrawer(group.id)" class="button">进入管理</el-button>
						</div>
					</div>
				</el-card>
			</el-col>
		</el-row>

		<el-dialog :visible.sync="showAdd" width="700px" append-to-body @close="addDialogClose">

			<el-form ref="form" :model="form" :rules="rules" label-width="100px" style="margin-right:50px;">

				<el-form-item label="组备注" prop="remark">
					<el-input v-model="form.remark" maxlength="15" placeholder="请输入备注" clearable>
					</el-input>
				</el-form-item>

				<el-form-item label="挂号科" prop="subjectId">
					<el-select v-model="form.subjectId" placeholder="请选择科" :clearable="true"
						@change="subjectChange(form.subjectId)">
						<el-option v-for="item in subject" :key="item.deptCode
					" :label="item.deptName" :value="item.deptCode
						">
						</el-option>
					</el-select>
				</el-form-item>

				<el-form-item label="挂号详细科" prop="subjects">
					<el-select style="margin-right: 20px;" v-model="form.subjectsId"
						:disabled="form.subjectId=='' ? true : false" placeholder="请选择详细科" :clearable="true"
						@change="subjectsChange(form.subjectsId)">
						<el-option v-for="item in subjects" :key="item.deptCode
								" :label="item.deptName" :value="item.deptCode
									">
						</el-option>
					</el-select>
				</el-form-item>

				<el-form-item label="挂号医生" prop="docterId">
					<el-select v-model="form.docterId" :disabled="form.subjectsId=='' ? true : false"
						placeholder="请选择医生" :clearable="true">
						<el-option v-for="item in docter" :key="item.docCode
							" :label="item.docName+'('+item.docDuty+')'" :value="item.docCode
								">
						</el-option>
					</el-select>
				</el-form-item>

				<el-form-item label="挂号日期" prop="registerDate">
					<el-col :span="11">
						<el-date-picker v-model="form.registerDate" format="yyyy 年 MM 月 dd 日" value-format="yyyy-MM-dd"
							type="date" placeholder="选择日期">
						</el-date-picker>
					</el-col>
					<el-col class="line" :span="2"></el-col>

					<el-col :span="11">
						<el-select v-model="form.registerTime" placeholder="默认没有随机选择" :clearable="true">
							<el-option v-for="item in registerTime" :key="item.v
								" :label="item.k" :value="item.v
									">
							</el-option>
						</el-select>
					</el-col>

				</el-form-item>

				<el-form-item label="开始时间" prop="startTime">
					<el-time-picker v-model="form.startTime" value-format="timestamp" placeholder="选择时间">
					</el-time-picker>
				</el-form-item>
			</el-form>

			<div>
				<el-button @click="submitForm" type="primary" size="medium" plain style="margin-right: 15px;">创 建
				</el-button>
				<el-button @click='reset' type="info" size="medium" plain>重 置</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<style>
	.status {
		border-radius: 4px;
		display: flex;
		align-items: center;
		padding: 4px 8px;
		font-size: 12px;
	}

	.status:before {
		content: "";
		width: 4px;
		height: 4px;
		border-radius: 50%;
		margin-right: 4px;
	}

	.status.active {
		color: #2ba972;
		background-color: rgba(43, 169, 114, 0.2);
	}

	.status.active:before {
		background-color: #2ba972;
	}

	.status.disabled {
		color: #59719d;
		background-color: rgba(89, 113, 157, 0.2);
	}

	.status.disabled:before {
		background-color: #59719d;
	}

	.group_row {
		margin-bottom: 15px;
	}

	#content {
		margin: 30px 0px 0px 0px;
	}

	#content:hover {
		transform: scale(1.01);
	}

	.time {
		font-size: 13px;
		color: #999;
	}

	.bottom {
		margin-top: 13px;
		line-height: 12px;
	}

	.button {
		padding: 0;
		float: right;
	}

	.image {
		width: 100%;
		height: 200px;
		display: block;
		background-size: 100%;
	}

	.clearfix:before,
	.clearfix:after {
		display: table;
		content: "";
	}

	.clearfix:after {
		clear: both
	}
</style>

<script>
	export default {
		data() {
			return {
				form: {
					remark: '',
					subjectId: '',
					subjectsId: '',
					docterId: '',
					registerDate: '',
					registerTime: '',
					startTime: ''
				},
				addGroupUserForm: {
					selectActiveUser: null,
					selectGroupId: null
				},
				drawerShow: false,
				showAdd: false,
				subject: null,
				subjects: null,
				docter: null,
				groupData: null,
				userData: null,
				drawerGroupId: null,
				groupInsideUserData: null,
				registerTime: [
					{
						k: '上午',
						v: 0,

					},
					{
						k: '下午',
						v: 1,

					},
					{
						k: '前夜',
						v: 2,

					}
				],
				rules: {
					remark: [
						{ required: true, message: '请输入组名称', trigger: 'blur' },
						{ min: 1, max: 15, message: '长度在 1 到 15 个字符', trigger: 'blur' },

					],
					subjectId: [
						{ required: true, message: '请选择科', trigger: 'change' }

					],
					subjectsId: [
						{ required: true, message: '请选择详细科', trigger: 'change' }

					],
					docterId: [
						{ required: true, message: '请选择医生', trigger: 'change' }
					],
					registerDate: [
						{ type: 'date', required: true, message: '请选择挂号日期', trigger: 'change' }
					],
					registerTime: [
						{ type: 'date', required: true, message: '请选择挂号时间', trigger: 'change' }
					],
					startTime: [
						{ type: 'date', required: true, message: '请选择开始时间', trigger: 'change' }
					]

				}
			};
		},
		created() {
			this.getSubject()
			this.getGroupList();
			setInterval(() => {
				this.getGroupList();
			}, 10000)

			this.getUserList();
		},
		methods: {
			//删除组内用户
			delectUser(groupId, snowflakeId) {
				this.$http.get("group/delect/" + groupId + "/" + snowflakeId).then(res => {
					if (res.data.code != 200) {
						this.error(res.data)
						return null;
					} else {
						this.getGroupList();
						this.success(res.data)
						this.getGroupUserList(groupId)
					}
				})
			},

			//暂停或启用
			stopUser(groupId, snowflakeId) {
				this.$http.get("group/stop/user/" + groupId + "/" + snowflakeId).then(res => {
					if (res.data.code != 200) {
						this.error(res.data)
						return null;
					} else {
						this.getGroupList();
						this.success({ msg: '操作成功!' })
						this.getGroupUserList(groupId)
					}
				})
			},

			//删除组
			delectGroup() {
				const loading = this.$loading({
					lock: true,
					text: '拼命处理中...',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
				this.$http.get("group/delect/" + this.drawerGroupId).then(res => {
					if (res.data.code != 200) {
						this.error(res.data)
						return null;
					} else {
						this.drawerShow = false;
						this.getGroupList();
						this.success({ msg: '删除成功!' })

					}
					loading.close()
				})

			},

			//打开弹窗
			openDrawer(groupId) {
				this.drawerShow = true;
				this.drawerGroupId = groupId;
				this.addGroupUserForm.selectGroupId = groupId;
				this.getGroupUserList(groupId)
			},

			//获取组内用户
			getGroupUserList(groupId) {
				this.$http.get("/group/inside/user/" + groupId).then(res => {
					if (res.data.code != 200) {
						this.error({ msg: '获取组内用户数据失败！' })
						return null;
					}
					this.groupInsideUserData = res.data.data
				})
			},

			//添加用户到组
			addGroupUser() {

				this.$http.post("/group/addUser", this.addGroupUserForm).then(res => {
					if (res.data.code != 200) {
						this.error(res.data.data)
						return null;
					}
					this.success({ msg: '添加成功！' })
					this.subjects = res.data.data
					this.getGroupUserList(this.addGroupUserForm.selectGroupId)

				})
			},

			//用户列表选中值
			handleSelectionChange(arr) {
				var userIdArr = new Array();
				for (var index in arr) {
					userIdArr[index] = { id: arr[index].id }
				}
				this.addGroupUserForm.selectActiveUser = userIdArr;
			},

			//打开弹窗
			openGroupDrawer() {
				this.addGroupUserForm.selectActiveUser = [];
			},


			addDialogClose() {
				this.form.subject = ''
				this.form.subjects = ''
				this.form.docter = ''
			},

			subjectChange(id) {
				this.$http.get("/docter/subjects/" + id).then(res => {
					if (res.data.data.length < 1) {
						this.error({ msg: '获取详细科数据失败！' })
						return null;
					}
					this.subjects = res.data.data
				})
			},

			subjectsChange(id) {
				this.$http.get("/docter/docter/" + id).then(res => {
					if (res.data.data.length < 1) {
						this.error({ msg: '获取医生数据失败！' })
						return null;
					}
					this.docter = res.data.data
				})
			},

			getSubject() {
				this.$http.get("/docter/subject").then(res => {
					if (res.data.data.length < 1) {
						this.error({ msg: '获取医生数据失败！' })
						return null;
					}
					this.subject = res.data.data
				})
			},

			deleteRow(index, rows) {
				this.remove(index, rows);
			},

			stop(index, rows) {
				this.$http.get("/user/stop/" + rows[index].cardId).then(res => {
					if (res.data.code == 200) {
						this.success(res.data)
						this.getList()
					} else {
						this.error(res.data)
					}

				})

			},

			remove(index, rows) {
				this.$http.get("/user/remove/" + rows[index].cardId).then(res => {
					if (res.data.code == 200) {
						this.success(rows[index].cardId)
						rows.splice(index, 1)
					} else {
						this.error(res.data)
					}
				})
			},

			success(s) {
				this.$notify({
					title: '成功通知',
					message: s.msg,
					type: 'success',
					duration: 2000,
					showClose: true
				});
			},

			error(e) {
				if (e.data != null) {
					this.$notify({
						title: e.msg,
						message: e.data,
						type: 'error',
						duration: 4000,
						showClose: true
					})
				} else {
					this.$notify({
						title: "失败通知",
						message: e.msg,
						type: 'error',
						duration: 4000,
						showClose: true
					})
				}

			},

			getGroupList() {
				this.$http.get('/group/list').then(res => {
					if (res.data.code == 200) {
						this.groupData = res.data.data
						if (res.data.data.length < 1) {
							return null;
						}
					} else {
						this.error(res.data)
					}
				});
			},

			getUserList() {
				this.$http.get('/user/list').then(res => {
					if (res.data.code == 200) {
						this.userData = res.data.data
						if (res.data.data.length < 1) {
							return null;
						}

					} else {
						this.error(res.data)
					}
				});
			},

			handleAdd() {
				this.reset();
				this.showAdd = true;
			},

			handleStop(row) {
				this.$http.get('/del', { url: row.url }).then(res => {
					if (res.data.code == 200) {
						this.$message.success(res.data.msg);
						this.getList();
					}
				});
			},

			submitForm() {
				const loading = this.$loading({
					lock: true,
					text: '拼命处理中...',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});

				this.$http.post('/group/create', this.form).then(res => {
					loading.close()
					if (res.data.code == 200) {
						this.success({ msg: '创建组成功！' })
						this.showAdd = false;
						this.getGroupList()
					} else {
						this.error(res.data)
					}
				});
			},


			// 表单重置
			reset() {
				this.form.subject = ''
				this.form.subjects = ''
				this.form.docter = ''
				this.form.registerDate = ''
				this.form.registerDate = ''
				this.form.startTime = ''
				this.form.subject = ''

			}
		}
	};
</script>